@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{text-align:center;margin:0;padding:0;box-sizing:border-box}.App-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}

.App-header{display:flex;flex-direction:row;justify-content:space-between;box-shadow:0px 0px 10px 0px #AFAFAF}.Header-logo{width:16vw;min-width:170px;margin-left:30px;margin-top:17px;margin-bottom:12px}.Header-right{display:flex;flex-direction:row;align-items:center;margin-right:15px}.Header-link{width:50px}@media screen and (max-width: 780px){.Header-logo{margin:15px auto 10px auto;width:55vw;max-width:285px}}

.Header-links{list-style:none}.Header-links-horizontal{display:flex;flex-direction:row;font-size:13px}.Header-links-horizontal li{margin-left:5px;margin-right:5px;font-family:'Roboto', sans-serif}.Header-links-horizontal li span{cursor:pointer;position:relative;display:block;text-transform:uppercase;margin:0px 0;padding:10px 15px;text-decoration:none;color:#262626;font-family:'Roboto', sans-serif;font-weight:500;transition:.5s;z-index:1}.Header-links-horizontal li span:before{content:'';position:absolute;top:0;left:0;width:100%;height:100%;border-top:2px solid #262626;border-bottom:2px solid #262626;transform:scaleY(2);opacity:0;transition:.3s}.Header-links-horizontal li span:after{content:'';position:absolute;top:2px;left:0;width:100%;height:100%;background-color:#262626;transform:scale(0);opacity:0;transition:.3s;z-index:-1}.Header-links-horizontal li span:hover{color:#fff}.Header-links-horizontal li span:hover:before{transform:scaleY(1);opacity:1}.Header-links-horizontal li span:hover:after{transform:scaleY(1);opacity:1}.Header-links-vertical{display:flex;flex-direction:column;padding-top:20px;padding-bottom:20px;margin:0;padding-left:0;padding-right:0}.Header-links-vertical li{margin:0;padding:20px;cursor:pointer}.Header-links-vertical li:hover{transition-duration:0.2s;background-color:#262626;color:#fff}.Header-links-vertical li span{font-size:13px;font-weight:600}@media screen and (max-width: 1200px){li{font-size:calc(13px + 7 * ((100vw - 1200px) / 800))}}

.Navigation-container{width:100%;position:absolute;background-color:white;box-shadow:0px 10px 13px 0px #afafaf36}.Container-visible{display:block}.Container-invisible{display:none}

